import React, { Component } from 'react';

const PICS = [
    `${process.env.PUBLIC_URL}/media/image/foto-1.jpg`,
    `${process.env.PUBLIC_URL}/media/image/foto-2.jpg`,
    `${process.env.PUBLIC_URL}/media/image/foto-3.jpg`
]

export default class HeroShop extends Component {

    state = {
        pic: PICS[0]
    }

    renderPics(){
        return (
            <div className="flex-1 flex vertical-horizontal pics">
                {
                    PICS.map((pic, idx) => (
                        <div className="flex-1 flex flex-center" key={idx} onClick={() => this.setState({ pic })}>
                            <img alt={`ph-${idx}`} src={pic} width="80%" height="80%" />
                        </div>
                    ))
                }
            </div>
        )
    }

    renderMainPic(){
        return (
            <div className="flex-3 flex flex-center">
                <img alt="ph-main-1" src={this.state.pic} width="80%" height="80%" />
            </div>
        )
    }

    renderData(){
        return (
            <div className="flex-3 flex vertical">

                <br />

                <h2>Caixa Plástica Fechada 130 litros</h2>
                <span><span role="img" aria-labelledby="stars">⭐️⭐️⭐️⭐️</span> (3 avaliações)</span>

                <hr />

                <div className="flex vertical">
                    <span>Opções - Cores: *</span>
                    <select className="form-input width-80" name="cores">
                        <option>Azul - +R$ 14,90</option>
                        <option>Branco Leitoso - +R$ 14,90</option>
                        <option>Branco Natural - +R$ 70,56</option>
                        <option>Café</option>
                        <option>Preto</option>
                    </select>
                </div>

                <br />

                <h1 className="price"><span>R$</span> 148,95</h1>

                <button className="button-cta">
                    COMPRAR
                </button>

                <br />

                <div>
                    <span>
                        Adicionar a lista de desejo | Comparar
                    </span>
                </div>

            </div>
        )
    }

    render(){
        return (
            <div className="HeroShop">
                <div className="container-big flex horizontal">
                    { this.renderPics() }
                    { this.renderMainPic() }
                    { this.renderData() }                  
                </div>
            </div>
        )
    }
}